/* Sizes */
$container: var(--containerWidth, 1310px);
$container-single: var(--containerSingleWidth, 830px);
$max-width-desktop: 95%;
$offset-gap: 50px;

$half-container-with: calc( ( ( ( 100vw - var(--scrollBarWidth) ) - #{$container} ) / 2 ) + #{$offset-gap} );
$half-container-with2: calc( ( ( ( 100vw - var(--scrollBarWidth) ) - 95vw ) / 2 ) + #{$offset-gap} );
$a-quater-with-1: calc( ( ( ( ( 100vw - var(--scrollBarWidth) ) - #{$container} ) / 2 ) + #{$offset-gap} ) / 2 );
$a-quater-with-2: calc( ( ( ( ( 100vw - var(--scrollBarWidth) ) - 95vw ) / 2 ) + #{$offset-gap} ) / 2 );

$half-container-with-fixed: calc( ( ( ( 1600px - var(--scrollBarWidth) ) - #{$container} ) / 2 ) + #{$offset-gap} );
$max-width-offset: calc( #{$container} + #{$half-container-with-fixed} - ( #{$offset-gap} * 2 ) );