@import 'variables';

#top{
    --singleContainer: #{$container-single};
    --singleSpace: 30px;
    --columnSpace: 0.75rem;
    .single-content-section{
        .container{
            max-width: 100% !important;
            width: 100% !important;
            padding-left: 0;
            padding-right: 0;
            .content{
                .entry-content-wrapper{
                    & > *:not(.alignwide):not(.alignfull):not(.alignleft):not(.alignright):not(.is-style-wide):not(.woocommerce),
                    *[class*=inner-container] > *:not(.entry-content):not(.alignwide):not(.alignfull):not(.alignleft):not(.alignright):not(.is-style-wide):not(.woocommerce),
                    & > figure.wp-block-image,
                    .default-max-width{
                        max-width: var( --singleContainer );
                        margin-left: auto;
                        margin-right: auto;
                        @media only screen and (max-width: calc( #{$container-single} + 100px ) ){
                            max-width: calc( 100% - 100px );
                        }
                        @media only screen and (max-width: 767px ){
                            max-width: 85%;
                        }
                    }
                    .alignwide{
                        max-width: calc( var( --singleContainer ) * 1.3 );
                        margin-left: auto;
                        margin-right: auto;
                        @media only screen and (max-width: calc( ( #{$container-single} * 1.3 ) + 100px ) ){
                            max-width: calc( 100% - 100px );
                        }
                        @media only screen and (max-width: 767px ){
                            max-width: 85%;
                        }
                    }
                    & > *{
                        margin-top: var( --singleSpace );
                        margin-bottom: var( --singleSpace );
                    }
                    .aligncenter{
                        clear: both;
                        display: block;
                        float: none;
                        margin-right: auto;
                        margin-left: auto;
                        text-align: center;
                    }
                    .wp-block-image{
                        &.alignfull,
                        &.alignwide{
                            img{
                                width: 100%;
                            }
                        }
                        .alignleft,
                        .alignright{
                            @media only screen and (min-width: 768px ){
                                max-width: 50%;
                            }
                        }
                        .alignleft{
                            float: left;
                            margin: 10px var( --singleSpace ) 10px 0;
                        }
                        .aligncenter{
                            margin-left: auto;
                            margin-right: auto;
                        }
                        .alignright{
                            float: right;
                            margin: 10px 0 10px var( --singleSpace );
                        }
                    }
                    figure.wp-block-image{
                        &.alignfull,
                        &.alignwide{
                            img{
                                width: 100%;
                            }
                        }
                        &.alignleft,
                        &.alignright{
                            img{
                                @media only screen and (min-width: 768px ){
                                    max-width: 50%;
                                }
                            }
                        }
                        &.alignleft{
                            img{
                                float: left;
                                margin: 10px var( --singleSpace ) 10px 0;
                            }
                        }
                        &.aligncenter{
                            img{
                                margin-left: auto;
                                margin-right: auto;
                            }
                        }
                        &.alignright{
                            img{
                                float: right;
                                margin: 10px 0 10px var( --singleSpace );
                            }
                        }
                    }
                    .wp-block-cover,
                    .wp-block-cover-image{
                        position: relative;
                        background-size: cover;
                        background-position: 50%;
                        min-height: calc( var( --singleContainer ) * 0.75 );
                        width: 100%;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        padding: 1em;
                    }
                    .wp-block-cover-image img.wp-block-cover__image-background,
                    .wp-block-cover-image video.wp-block-cover__video-background,
                    .wp-block-cover img.wp-block-cover__image-background,
                    .wp-block-cover video.wp-block-cover__video-background{
                        position: absolute;
                        top: 0;
                        left: 0;
                        right: 0;
                        bottom: 0;
                        margin: 0;
                        padding: 0;
                        width: 100%;
                        height: 100%;
                        max-width: none;
                        max-height: none;
                        object-fit: cover;
                        outline: none;
                        border: none;
                        box-shadow: none;
                    }
                    .wp-block-cover__image-background,
                    .wp-block-cover__video-background{
                        z-index: 0;
                    }
                    .wp-block-cover-image .wp-block-cover__inner-container,
                    .wp-block-cover .wp-block-cover__inner-container{
                        z-index: 1;
                    }
                    .has-text-align-left{
                        text-align: left;
                    }
                    .has-text-align-center{
                        text-align: center;
                    }
                    .has-text-align-right{
                        text-align: right;
                    }
                    .wp-block-columns{
                        display: flex;
                        flex-wrap: wrap;
                        @media only screen and (min-width: 768px){
                            flex-wrap: nowrap;
                        }
                        .wp-block-column{
                            @media only screen and (max-width: 767px){
                                width: 100%;
                                flex-basis: unset !important;
                                margin-bottom: var( --columnSpace );
                            }
                            @media only screen and (min-width: 768px){
                                flex: 1;
                            }
                            > *{
                                margin-top: var( --columnSpace );
                                margin-bottom: var( --columnSpace );
                            }
                            > *:first-child{
                                margin-top: 0;
                            }
                            > *:last-child{
                                margin-bottom: 0;
                            }
                            &:not(:first-child){
                                @media only screen and (min-width: 768px){
                                    margin-left: var( --columnSpace );
                                }
                            }
                            img{
                                display: block;
                            }
                            &.is-vertically-aligned-top{
                                align-self: flex-start;
                            }
                            &.is-vertically-aligned-center{
                                align-self: center;
                            }
                            &.is-vertically-aligned-bottom{
                                align-self: flex-end;
                            }
                        }
                        &.are-vertically-aligned-top{
                            align-items: flex-start;
                        }
                        &.are-vertically-aligned-center{
                            align-items: center;
                        }
                        &.are-vertically-aligned-bottom{
                            align-items: flex-end;
                        }
                    }
                    .wp-block-gallery{
                        .blocks-gallery-grid{
                            display: flex;
                            flex-wrap: wrap;
                            margin: calc( var( --columnSpace ) * -1 );
                            img{
                                display: block;
                            }
                        }
                        @for $i from 1 through 8{
                            &.columns-#{$i}{
                                .blocks-gallery-item{
                                    width: calc( 100% / #{$i} );
                                    padding: var( --columnSpace );
                                    flex-grow: 1;
                                }
                            }
                        }
                    }
                    & > *:first-child:not(.post-img){
                        margin-top: 0 !important;
                    }
                    & > *:last-child{
                        margin-bottom: 0 !important;
                    }
                }
            }
        }
    }
}